<template>
  <div class="login-form-container">
    <header>
      <ul class="list-style flex-center">
        <li
          v-for="(item, index) in tabList"
          :key="index"
          class="list-item-style"
          :class="{ active: index === active }"
          @click="active = index"
        >
          {{ item }}
        </li>
      </ul>
    </header>

    <main>
      <!-- 手机号登录 -->
      <div v-show="active === 0" class="sms-login-style">
        <InputPhone :inputVal.sync="form.phone" @command="onCommandPhone" />

        <InputVerificationCode
          class="input-margin-style"
          :value.sync="form.code"
          @send="userAuthSendAPI(form.phone)"
        />
      </div>
      <!-- 账号密码登录 -->
      <div v-show="active === 1" class="password-login-style">
        <Input
          label="账   号"
          placeholder="请输入登录账号"
          :value.sync="form.phone"
        />
        <Input
          class="input-margin-style"
          label="密   码"
          placeholder="请输入密码"
          :value.sync="form.password"
          password
        />
      </div>

      <el-button class="login-btn-style" type="primary" round @click="onLogin">
        立即登录
      </el-button>

      <p v-show="active === 1" class="tips-style">忘记密码？请联系管理员...</p>
    </main>
  </div>
</template>

<script>
export default {
  components: {
    InputPhone: () => import("@/components/InputPhone"),
    InputVerificationCode: () => import("@/components/InputVerificationCode"),
    Input: () => import("@/components/Input"),
  },
  data() {
    return {
      // 当前选中的登录模式
      active: 0,
      // 登录模式选项
      tabList: ["短信登录", "密码登录"],
      form: {
        // 手机号
        phone: "",
        // 区号
        areaNum: "",
        // 验证码
        code: "",
        // 密码
        password: "",
      },
    };
  },
  methods: {
    /**
     * InputPhone-选择区号
     */
    onCommandPhone(val) {
      this.form.areaNum = val;
    },

    /**
     * 校验表单填写
     */
    validator() {
      if (this.active === 0) {
        if (this.form.phone === "" || this.form.code === "") {
          this.$message.error("请填写手机号与验证码");
          return false;
        }
      } else {
        if (this.form.phone === "" || this.form.password === "") {
          this.$message.error("请填写账号与密码");
          return false;
        }
      }

      return true;
    },

    /**
     * 登录按钮点击事件
     */
    onLogin() {
      const validator = this.validator();
      if (validator) {
        console.log("调用登录接口 :>>", this.form);
        this.$store.dispatch("user/userAuthPhone", {
          phone: this.form.phone,
          code: this.form.code,
        });
      }
    },

    /**
     * 获取短信验证码
     */
    async userAuthSendAPI(phone) {
      const data = {
        phone,
      };
      await this.$request("user.userAuthSend", data);
    },
  },
};
</script>

<style lang="scss" scoped>
.login-form-container {
  height: px(400);
  header {
    .list-style {
      .list-item-style {
        padding: 0 px(17);
        font-size: px(24);
        color: #999999;
        cursor: pointer;
        border-right: 1px solid #999;
      }
      :nth-last-child(1) {
        border: none;
      }
      .active {
        font-weight: bold;
        color: #333333;
      }
    }
  }
  main {
    margin: px(56) auto 0;
    width: px(320);
    .input-margin-style {
      margin-top: px(24);
    }
    .login-btn-style {
      margin-top: px(56);
      width: px(321);
      height: px(56);
      border-radius: px(28);
      font-size: px(16);
      font-weight: bold;
      color: #ffffff;
    }
    .tips-style {
      margin-top: px(16);
      font-size: px(12);
      color: #cccccc;
      text-align: center;
    }
  }
}
</style>
